import { Injectable } from '@angular/core';
import { Headers, Http } from '@angular/http';
import { Login } from '.././hero';
import 'rxjs/add/operator/toPromise';
import { Router } from '@angular/router';
import {CanActivate} from "@angular/router";

@Injectable()
export class AuthService {
 
 private BASE_URL: string = 'http://127.0.0.1:8000';
 private headers: Headers = new Headers({'Content-Type': 'application/json'});
 constructor(private http: Http,private router:Router) {

 }
 test(): string {
    return 'working';
	}
 login(data:Login): Promise<any> {
    let url: string = `http://127.0.0.1:8000/rest-auth/login/`;
    let key:any;
    let data1:any;
    console.log(data)
    return this.http.post(url,JSON.stringify(data), {headers: this.headers})
    .toPromise()
    .then(res => {
    key = res.json()['key']
    if(key){
        localStorage.setItem('currentUser',key);
    }
    data1=localStorage.getItem('currentUser');
               console.log(data1)
    this.router.navigate(['home'])
    })


    .catch(this.handleError);
  }



private handleError(error: any): Promise<any> {
  console.error('An error occurred', error); // for demo purposes only
  return Promise.reject(error.message || error);
}


isLoggedIn(): boolean {
    let data:any;
     data=localStorage.getItem('currentUser');
     if(data)
    return true;
    else
        return false;
}
}

export class AlwaysAuthGuard implements CanActivate {
  canActivate() {
    console.log("AlwaysAuthGuard");
    return true;
  }
}
 


@Injectable()
export class OnlyLoggedInUsersGuard implements CanActivate { 
  constructor(private userService:AuthService ) {}; 

  canActivate() {
    console.log("OnlyLoggedInUsers");
    if (this.userService.isLoggedIn()) { 
      return true;
    } else {
      window.alert("You don't have permission to view this page"); 
      return false;
    }
  }
}

@Injectable()
export class SignupGuard implements CanActivate { 
  constructor(private userService:AuthService ) {}; 

  canActivate() {
    if (this.userService.isLoggedIn()) {
      window.alert("You are already logged in"); 
      return false;
    } else {
      return true;
    }
  }
}




