import { NgModule }      from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule }   from '@angular/forms';

import { AppComponent } from './app.component';
import { HeroDetailComponent } from './hero-detail.component';
import { HeroesComponent } from './heroes.component';
import { HeroService } from './hero.service';
import { DashboardComponent } from './dashboard.component';
import { HeroSearchComponent } from './hero-search.component';
import {SigninComponent} from './signinform.component';
import { LoginComponent } from './login.component';
import { AuthService } from './services/auth.service';
import { HomeComponent } from './home.component';
import {LogoutComponent} from './logout.component';

import { AppRoutingModule }     from './app-routing.module';
import { HttpModule }    from '@angular/http';

import { AlwaysAuthGuard,OnlyLoggedInUsersGuard,SignupGuard } from './services/auth.service';

import {platformBrowserDynamic} from '@angular/platform-browser-dynamic';

import { ReactiveFormsModule } from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';

@NgModule({
  declarations: [
    AppComponent,
    HeroDetailComponent,
    HeroesComponent,
    DashboardComponent,
    HeroSearchComponent,
    SigninComponent,
    LoginComponent,
    HomeComponent,
    LogoutComponent,
  ],
  imports: [
    BrowserModule,
    FormsModule,
    AppRoutingModule,
    HttpModule,
    HttpClientModule,
    ReactiveFormsModule,

  ],
  providers: [ HeroService,AuthService, AlwaysAuthGuard,OnlyLoggedInUsersGuard,SignupGuard],
  bootstrap: [ AppComponent ]
})


export class AppModule { 
}
platformBrowserDynamic().bootstrapModule(AppModule);



