import { Component, OnInit } from '@angular/core';
import { AuthService } from './services/auth.service';
import { ViewChild } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'login',
  template:`
  <div class="row">
  <div class="col-md-4">
    <h1>Login</h1>
    <hr><br>
    <form #log="ngForm" (ngSubmit)="onLogin()" novalidate>
     <div class="form-group">
       <label for="username">Username</label>
       <input type="text" class="form-control" id="username" placeholder="enter username" ngModel name="username" required>
     </div>
      
     <div class="form-group">
       <label for="password">Password</label>
       <input type="password" class="form-control" id="password" placeholder="enter password" ngModel name="password" required>
     </div>
     <button type="submit" class="btn btn-default">Submit</button>
    </form>
    <p>New User ?? <a routerLink="/signin" routerLinkActive="active">Signin</a></p>
  </div>
</div>
  `
})

export class LoginComponent implements OnInit {
  test: string = 'just a test';
  constructor(private auth: AuthService,private router:Router) {}

  @ViewChild('log') form: any;
  onLogin(): void {
    this.auth.login(this.form.value)
  }

  ngOnInit(): void {

  }

  }



